export const Constants = {

  /**
   * The ms-rest version
   * @const
   * @type {string}
   */
  msRestVersion: '3.0.0',

  /**
   * Specifies HTTP.
   *
   * @const
   * @type {string}
   */
  HTTP: 'http:',

  /**
   * Specifies HTTPS.
   *
   * @const
   * @type {string}
   */
  HTTPS: 'https:',

  /**
   * Specifies HTTP Proxy.
   *
   * @const
   * @type {string}
   */
  HTTP_PROXY: 'HTTP_PROXY',

  /**
   * Specifies HTTPS Proxy.
   *
   * @const
   * @type {string}
   */
  HTTPS_PROXY: 'HTTPS_PROXY',

  HttpConstants: {
    /**
     * Http Verbs
     *
     * @const
     * @enum {string}
     */
    HttpVerbs: {
      PUT: 'PUT',
      GET: 'GET',
      DELETE: 'DELETE',
      POST: 'POST',
      MERGE: 'MERGE',
      HEAD: 'HEAD',
      PATCH: 'PATCH'
    },
  },

  /**
   * Defines constants for use with HTTP headers.
   */
  HeaderConstants: {
    /**
     * The Authorization header.
     *
     * @const
     * @type {string}
     */
    AUTHORIZATION: 'authorization',

    AUTHORIZATION_SCHEME: 'Bearer',

    /**
     * The UserAgent header.
     *
     * @const
     * @type {string}
     */
    USER_AGENT: 'User-Agent'
  }
};



